.slider__wrapper {
  display: flex;
  margin-top: 50px;
  margin-bottom: 50px;

  .slider__item {
    @media (max-width: $max-tablet-width) {
      width: 324px;
    }

    @media (max-width: $max-mobile-width) {
      width: 130px;
    }
  }
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: $white;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;

  a {
    width: 100%;
    border-bottom: none;
  }

  img {
    @media (max-width: $max-tablet-width) {
      width: 324px;
      height: 340px;
    }

    @media (max-width: $max-mobile-width) {
      width: 130px;
      height: 136px;
    }
  }
}

.swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
  color: $medium-grey;
  opacity: 1;
  background-color: transparent;
}

.swiper-pagination-bullet-active {
  color: $black;
  background-color: transparent;
}

.swiper-button-next::after,
.swiper-container-rtl .swiper-button-prev::after,
.swiper-button-prev::after,
.swiper-container-rtl .swiper-button-next::after {
  display: none;
}

.swiper-button-next {
  svg {
    transform: rotate(180deg);
  }
}

.slider__buttons {
  position: absolute;
  width: 100%;
  height: 50px;
  top: 50px;
}

.slider__button-next,
.slider__button-prev {
  top: 10px;
  right: 0;
  background-color: transparent;

  &:hover,
  &:active {
    border: none;
    opacity: 0.3;
  }
}
